import { AnyAction } from 'redux';
import { FETCH_ALL, CREATE, UPDATE, DELETE, LIKE } from '../constants/actionTypes';

const initialState = [{
  title: "",
  info: "",
  price: 0,
  _id: "",
  selectedFile: "",
},]

const postsReducer = (posts = initialState, action: AnyAction): any => {
  switch (action.type) {
    case FETCH_ALL:
      return action.payload;
    case LIKE:
      return posts.map((post: { _id: any; }) => (post._id === action.payload._id ? action.payload : post));
    case CREATE:
      return [...posts, action.payload];
    case UPDATE:
      return posts.map((post: { _id: any; }) => (post._id === action.payload._id ? action.payload : post));
    case DELETE:
      return posts.filter((post: { _id: any; }) => post._id !== action.payload);
    default:
      return posts;
  }
};

export default postsReducer;